html {
  font-size: 62.5%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  overflow: hidden;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

#rotatingWords {
  display: inline-block;
}

.typeText {
  margin: 0;
  color: white;
}

.old {
  display: none;
}

.gsapCursor.blink {
  display: inline-block;
  animation: 1s blinkCursor step-end infinite;
}

.gsapCursor.hide {
  display: none;
}

@keyframes blinkCursor {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}
.about,
.projects,
.contact {
  scroll-snap-align: center;
  scroll-snap-stop: always;
}

.diagonal-separator::before,
.diagonal-separator::after {
  position: absolute;
  content: "";
  pointer-events: none;
}

.diagonal-separator {
  z-index: 1;
  background: #2c3e50;
}

.diagonal-separator {
  position: relative;
  color: #fff;
  text-align: center;
}

.diagonal-separator::before,
.diagonal-separator::after {
  top: 0;
  left: -25%;
  z-index: -1;
  width: 150%;
  height: 75%;
  background: inherit;
  -webkit-transform: rotate(-2deg);
  transform: rotate(-2deg);
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
}

.diagonal-separator::before {
  height: 50%;
  background: #116094;
  -webkit-transform: rotate(-3deg);
  transform: rotate(-3deg);
  -webkit-transform-origin: 3% 0;
  transform-origin: 3% 0;
}

.menu-bar {
  background-image: linear-gradient(-90deg, #d38312, #a83279);
}

.nav-link {
  font-size: 16px;
