.nav-pane {
  transition: 250ms;
  position: fixed;
  transform: translateX(-350px);
  top: 0;
  height: 100vh;
  width: 348px;
  background: #415b76;
  border-right: 2px solid #3d566f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0px 3px 10px rgba(33, 33, 33, 0.5);
  z-index: 999;
}
@media screen and (max-width: 620px) {
  .nav-pane {
    width: 298px;
    transform: translateX(-300px);
  }
}
.nav-pane a {
  margin: 0;
  color: #f1f1f1;
  text-transform: uppercase;
  position: relative;
  cursor: pointer;
  font-size: 1.8rem;
  padding: 15px 30px;
  transition: all 250ms;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.nav-pane a:hover {
  padding: 15px 45px;
  background-color: rgba(0, 0, 0, 0.2);
}
.nav-pane.open {
  transform: translateX(0);
  transition: 250ms;
}

.toggle-nav {
  position: fixed;
  top: 25px;
  right: 25px;
  cursor: pointer;
  color: #212121;
  z-index: 999;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.toggle-nav.open .m,
.toggle-nav.open .n,
.toggle-nav.open .u {
  opacity: 1;
}
.toggle-nav .m,
.toggle-nav .n,
.toggle-nav .u {
  color: #fff;
  font-size: 1.05rem;
  opacity: 0;
  transition: 250ms;
  letter-spacing: 2px;
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE10+/Edge */
  user-select: none;
  /* Standard */
}
.toggle-nav .e {
  display: inline-block;
  height: 12px;
  width: 17px;
}
.toggle-nav .e.btn-cerrar .bar:nth-child(1) {
  transition: 250ms;
  transform: rotate(45deg);
  top: 5px;
}
.toggle-nav .e.btn-cerrar .bar:nth-child(2) {
  opacity: 0;
  transition: 250ms;
}
.toggle-nav .e.btn-cerrar .bar:nth-child(3) {
  transition: 250ms;
  transform: rotate(-45deg);
  bottom: 5px;
}
.toggle-nav .e .bar {
  transition: 250ms;
  position: relative;
  display: block;
  background: #e7e7e7;
  border-radius: 1px;
  border-bottom: 1px solid #b2b2b2;
  width: 15px;
  height: 2px;
}
.toggle-nav .e .bar:nth-child(2) {
  margin: 3px 0;
}

.toggle-nav:hover .m,
.toggle-nav:hover .n,
.toggle-nav:hover .u {
  opacity: 1;
  transition: 250ms;
}